import React, { useState } from "react";
import "./Calendar.css";

function getDaysInMonth(year, month) {
  return new Date(year, month + 1, 0).getDate();
}

function getFirstDayOfWeek(year, month) {
  return new Date(year, month, 1).getDay();
}

const WEEKDAYS = ["Sun", "Mon", "Tue", "Wed", "Thu", "Fri", "Sat"];

// Set this to true for testing—all dates become accessible
const testMode = false;

const Calendar = ({ onDateClick, countdownEnd }) => {
  const [displayedYear, setDisplayedYear] = useState(2025);
  const [displayedMonth, setDisplayedMonth] = useState(4); // May (0-indexed)

  const today = new Date();
  today.setHours(0, 0, 0, 0);

  const daysInMonth = getDaysInMonth(displayedYear, displayedMonth);
  const firstDay = getFirstDayOfWeek(displayedYear, displayedMonth);

  const [breaking, setBreaking] = useState(false);
  const [unlocked, setUnlocked] = useState(false);

  const todayDateString = `${today.getFullYear()}-${String(today.getMonth() + 1).padStart(2, "0")}-${String(today.getDate()).padStart(2, "0")}`;

  const handleOwlClick = () => {
    if (!breaking) {
      setBreaking(true);
      setTimeout(() => {
        setUnlocked(true);
        setBreaking(false);
        onDateClick(todayDateString);
      }, 1000);
    }
  };

  const isMay = displayedYear === 2025 && displayedMonth === 4;
  const isJune = displayedYear === 2025 && displayedMonth === 5;

  const handlePrevMonth = () => {
    if (isJune) {
      setDisplayedMonth(4); // Go to May
      setDisplayedYear(2025);
    }
  };

  const handleNextMonth = () => {
    if (isMay) {
      setDisplayedMonth(5); // Go to June
      setDisplayedYear(2025);
    }
  };

  // Build calendar cells
  const calendarCells = [];
  for (let i = 0; i < firstDay; i++) {
    calendarCells.push(<div key={`empty-${i}`} className="calendar-cell empty"></div>);
  }
  for (let day = 1; day <= daysInMonth; day++) {
    const dateObj = new Date(displayedYear, displayedMonth, day);
    dateObj.setHours(0, 0, 0, 0);

    const isToday =
      day === today.getDate() &&
      displayedMonth === today.getMonth() &&
      displayedYear === today.getFullYear();

    // If testMode is true, all days are accessible (not "future")
    const isFuture = testMode ? false : dateObj > today;
    const showOwl = isFuture || (isToday && !unlocked);

    const cellDateString = `${displayedYear}-${String(displayedMonth + 1).padStart(2, "0")}-${String(day).padStart(2, "0")}`;

    // Special golden glow for June 16, 2025
    const isGoldenDay = displayedYear === 2025 && displayedMonth === 5 && day === 16;

    calendarCells.push(
      <div
        key={day}
        className={
          `calendar-cell${isToday ? " today" : ""}${isFuture ? " disabled" : ""}${isGoldenDay ? " golden-glow" : ""}`
        }
        onClick={() => {
          // If testMode, allow all; else, only allow if not future and (for today) if unlocked
          if (testMode || (!isFuture && (!isToday || unlocked))) {
            onDateClick(cellDateString);
          }
        }}
        style={{ cursor: (!isFuture || testMode) ? "pointer" : "not-allowed" }}
      >
        {day}
        {showOwl && (
          <img
            src={process.env.PUBLIC_URL + "/images/owl-cave.png"}
            alt="Owl Cave Symbol"
            className={`owl-cave-cell${isToday && breaking ? " breaking" : ""}`}
            onClick={isToday
              ? (e) => {
                  e.stopPropagation();
                  handleOwlClick();
                }
              : undefined}
            style={{
              zIndex: 10,
              pointerEvents: isToday ? "auto" : "none",
              cursor: isToday ? "pointer" : "default"
            }}
          />
        )}
      </div>
    );
  }

  const monthName = new Date(displayedYear, displayedMonth).toLocaleString("default", { month: "long" });

  return (
    <div className="calendar-container" style={{ position: "relative", overflow: "hidden" }}>
      {/* Falling blossoms */}
      <img src={process.env.PUBLIC_URL + "/images/blossom.png"} className="cherry-blossom" alt="blossom" />
      <img src={process.env.PUBLIC_URL + "/images/blossom.png"} className="cherry-blossom" alt="blossom" />
      <img src={process.env.PUBLIC_URL + "/images/blossom.png"} className="cherry-blossom" alt="blossom" />
      <img src={process.env.PUBLIC_URL + "/images/blossom.png"} className="cherry-blossom" alt="blossom" />
      <img src={process.env.PUBLIC_URL + "/images/blossom.png"} className="cherry-blossom" alt="blossom" />

      {/* Calendar header and navigation */}
      <div className="calendar-header" style={{ display: "flex", alignItems: "center", justifyContent: "center", gap: "16px" }}>
        <button
          className="calendar-nav"
          onClick={handlePrevMonth}
          aria-label="Previous month"
          disabled={isMay}
        >
          <span className="calendar-arrow">{'‹'}</span>
        </button>
        <h2 style={{ margin: 0 }}>
          {monthName} {displayedYear}
        </h2>
        <button
          className="calendar-nav"
          onClick={handleNextMonth}
          aria-label="Next month"
          disabled={isJune}
        >
          <span className="calendar-arrow">{'›'}</span>
        </button>
      </div>
      <div className="calendar-grid">
        {WEEKDAYS.map((wd) => (
          <div key={wd} className="calendar-cell header">
            {wd}
          </div>
        ))}
        {calendarCells}
      </div>
    </div>
  );
};

export default Calendar;
