"2025-05-27": {
  title: "The Way You See the World",
  text: `I still remember—one of the very first things I liked about you was your way of seeing the world.  
That feeling only grew stronger when you shared your Instagram with me, and I got to experience your photography for the first time.

You have a rare gift: an appreciation for darkness, for the subtle and the mysterious. Not many people can find beauty in the shadows, but you do, and it’s something I noticed from the very first moment I saw your pictures.  
But what I love even more is that you also recognize beauty on the bright side of life. You notice the glow in both the quiet and the vibrant, the gentle and the bold.

You have this wonderful ability to find meaning and wonder in every corner of the world, no matter how hidden or unexpected.  
It’s one of the things I cherish most about you—and I feel lucky every day to see the world a little differently because of you.`
},

"2025-05-28": {
  title: "Our Very First Conversation 🌲",
  text: `Do you remember our very first chat? I do—so clearly.

From the very beginning, our conversation felt different. Instead of the usual small talk and polite questions that come with meeting someone new, we dove right into stories that actually meant something. I still smile thinking about how you told me that story about leaving your friends in the woods. It was bold, a little mischievous, and completely unexpected—and it made me realize right away that you’re not like anyone else I’ve ever met.

That moment set the tone for everything that followed. We skipped over the surface and started bonding instantly, sharing real stories and laughter instead of just the usual “what do you do?” and “where are you from?” It was the first sign (of many) that we could be ourselves with each other from the very start.

Thank you for that. For being open, for being you, and for making our very first conversation one I’ll never forget.`},

