"2025-05-31": {
  title: "Mysterious perfume ✨",
  blocks: [
    {
      type: "image",
      src: process.env.PUBLIC_URL + '/images/bluredperfume.png',
      alt: "Fueguia 1833"
    },
    {
      type: "text",
      value: `||Valle de la Luna|| by Fueguia 1833 – the scent of a mysterious desert under the moon's glow.

Imagine the ||Valle de la Luna|| in Argentina – violet sands, mystical craters, and endless dunes dissolving in the silvery light of the night sky. In this world, the boundaries between earth and sky blur, and the air is filled with delicate invisible scents of wild nature.

||Iris|| and ||passionflower|| – like an unexpected floral touch in the silence of the desert. ||Sandalwood|| adds depth, creating a sensation of soft, woody warmth that awakens the senses.

Mineral and powdery accords transport you to a space where the air is crisp, like the cool breath of the night, and the fine dust carried by the wind feels like the touch of time itself.

||Valle de la Luna|| is more than just a fragrance; it is a portal to an infinite desert universe full of mystery and contrasts. Each breath reveals a new dimension.`
    }
  ]
},
