"2025-06-02": {
  title: "Spring Is Over",
  blocks: [
    {
      type: "text",
      value: `Spring isn’t officially gone yet, but after this weekend, I can feel that it’s already summer in Barcelona.

I want to take this moment to thank you for this spring. You may not realize it, but over these past months, I’ve learned so much from you—especially from the way you find beauty in life’s smallest details. This spring has felt different for me, and that’s because of your influence. Your photos of trees, flowers, and parks inspired me to truly notice the magic happening all around me. Of course, the highlight of it all was Edinburgh—a wonderful city, and the perfect time to visit.

This means a lot to me, more than you might know. You don’t know my whole story (not yet), but for a long time, I felt numb in many ways. Not so long ago, I wouldn’t have been able to express myself to anyone the way I do with you now. That path, with all its unexpected twists, brought its share of challenges, but I don’t regret any of it. One day, I’ll share my whole story with you—but not here, not yet.

I hope that when you arrive in Barcelona, it won’t be too late for spring. I’ve even found some fields of flowers where we could go for a walk together. I’d love nothing more than to experience that with you.

Thank you for making this spring truly special for me. I can honestly say it’s the first spring in my life that I’ve really, deeply enjoyed.`
    }
  ]
},
"2025-06-03": {
  title: "Your Mission: Curate Javier’s Signature Fragrance Wardrobe 💎",
  blocks: [
    {
      type: "text",
      value: `Today, it’s your turn to work again! I’d love to have my own personalized 💎 “Javier’s Signature Fragrance Wardrobe.” I know my collection isn’t as extensive as yours, but it would mean a lot if you could categorize all the perfumes I have, just as you did with your own.

Here’s my current lineup:

• Penhaligon's Mr. Sam  
• Penhaligon's Fortuitous Finley (not bought yet, but coming soon!)  
• The mysterious perfume you bought for me  
• Xerjoff Naxos  
• Jazz Club by Maison Margiela  
• Under the Stars by Maison Margiela  
• Pardon by Nasomatto  
• A Reason to Love by Gucci

If you notice any gaps in my collection, I’d really appreciate your expert advice—what type of fragrance am I missing to make my wardrobe more balanced? Please recommend two or three perfumes I should add to the very top of my wishlist.

Thank you for guiding me on this amazing olfactive journey!

P.S. I’ve decided to part ways with Khamrah Lattafa. I just don’t enjoy it anymore, and whenever I catch a whiff of it on the street, I don’t really like the people wearing it… So, I think it’s officially out of the collection! 😂`
    }
  ]
},
