"2025-06-12": {
  title: "A Moment for Haiku",
  blocks: [
    {
      type: "text",
      value: `Today, I want to share something a little different: a haiku.

A haiku is a traditional Japanese poem, famous for its three-line structure and its focus on simplicity and precision. But beyond the 5-7-5 syllable rule, the real essence of haiku is about capturing a fleeting moment—a blend of what we see and what we feel, distilled into just a few words. Haikus invite us to pause, notice the small details, and find beauty or meaning in the ordinary. They’re like tiny windows into the world, where a single image or sensation can hint at something much larger.

Rooted in Zen philosophy, haiku encourages us to let go of overthinking and just experience the present, allowing the reader to find their own meaning in the space between the words. It’s not about explaining everything, but about suggesting a feeling or a connection—between people, nature, and life itself.

So, here’s a haiku I wrote for you, and of course about you ( This time no AI was used at all):


Veil of quiet strength  
Silk and fire in her gaze  
Gentle, wild, and true


Let this small poem be a moment of reflection, a little pause in your day, and a reminder of the beauty found in simplicity.`
    }
  ]
},
"2025-06-13": {
  title: "A Story of Growth: The Path Back to Myself",
  blocks: [
    {
      type: "text",
      value: `Today I want to share a very personal story about my life and the journey of growth I've experienced over the past few years. Honestly, if I hadn’t gone through this path, none of this—this calendar, my note with the flowers, or even half of our conversations—would probably exist.

This is a story I haven’t really told anyone in full before. Of course, my friends and family know bits and pieces, but I don’t think I’ve ever shared the whole thing—except with my therapist, who, as you’ll see, played a very important role!

It starts with me as a child (don’t worry, nothing bad happened to me in church). I was a very sensitive kid. I remember crying a lot when something upset me, and feeling unable to respond in arguments—my throat would tighten up and I’d just start to cry. I wasn’t used to violence; my parents never hit me (which was actually common in Argentina back then), and they rarely even raised their voices. If I misbehaved, my punishments were things like no TV or no video games.

None of this was ever a problem until I started high school. I was in an all-boys class—35 boys, all together from age 12 to 18. As you might imagine, it wasn’t exactly a place where showing feelings was encouraged. It wasn’t the ideal environment for a sensitive boy. I never had social problems or bullying, because I’ve always been good at adapting. So I did what I had to do: I learned to stop feeling so much and started building walls around myself. I got very good at it, and honestly, I had a great time in high school and made a lot of friends—eight of whom I’m still close with today.

But all of this came at a cost. Something I only realized a few years ago: I paid for it by becoming numb. I lost the ability to really feel, and even to recognize what I was feeling.

A lot of time passed. I had several long relationships, but I went through them completely numb. Until one day in Barcelona, things started to change—without me even realizing it.

This was about five years ago, when my friend Fran invited me to my first electronic music party. At that party, and later, he introduced me to a drug called MDMA (the main ingredient in ecstasy pills). It’s a very curious drug—nothing like what I’d imagined. It’s called an empathogen, and it makes you feel love—a lot of it. For me, it was a feeling I hadn’t experienced in a very long time, but it was so strong and familiar that I recognized it immediately.

(Just a note: this is an illegal drug, but in the last decade, psychiatrists have started to seriously investigate its use in treatments—especially for things like PTSD—because it can be incredibly helpful in some cases.)

Of course, the drug didn’t “fix” me. But it was the trigger that made me start asking myself questions. A few months later, I decided to begin therapy—something I’d never considered before and honestly had a lot of prejudices about.

Since then, the journey hasn’t been easy. I was like a child, learning about feelings for the first time. And when I finally opened the gates and tore down all the walls I’d built, all my emotions came rushing in at once. I had to learn how to handle them and live with them—the good and the bad. It wasn’t free; it cost me my longest relationship, which, in hindsight, should have ended much sooner. But it was easy and convenient—perfect for the old, numb Javi.

Luckily, things are really good now. The path wasn’t easy, but I’m truly happy I took it. I don’t regret anything—I’ve recovered my true self. And as I said at the beginning, without this journey, we probably wouldn’t be talking right now.

I hope you enjoyed this story, and that it helps you know me a little better.`
    }
  ]
}




