import React, { useState, useEffect, useRef } from "react";
import ReactMarkdown from "react-markdown";
import emailjs from "emailjs-com";
import "./DayModal.css";

function processBlurredText(text) {
  // Splits text on ||word|| and wraps those words in a span
  return text.split(/(\|\|.*?\|\|)/g).map((part, idx) => {
    if (part.startsWith('||') && part.endsWith('||')) {
      const word = part.slice(2, -2);
      return <span key={idx} className="blurred-word">{word}</span>;
    }
    return part;
  });
}

function DayModal({ date, content = {}, onClose }) {
  const [comment, setComment] = useState("");
  const [sending, setSending] = useState(false);
  const [sent, setSent] = useState(false);
  const [answers, setAnswers] = useState([]);
  const audioRef = useRef(null);

  useEffect(() => {
    document.body.classList.add("modal-open");
    return () => {
      document.body.classList.remove("modal-open");
    };
  }, []);

  useEffect(() => {
    if (date === "2025-05-25" && audioRef.current) {
      audioRef.current.currentTime = 0;
      audioRef.current.play().catch(() => {});
    }
  }, [date]);

  useEffect(() => {
    if (date) {
      const saved = localStorage.getItem(`comment-${date}`);
      setComment(saved || "");
      setSent(false);

      if (content?.questions) {
        const savedAnswers = JSON.parse(localStorage.getItem(`answers-${date}`) || "[]");
        setAnswers(
          savedAnswers.length === content.questions.length
            ? savedAnswers
            : Array(content.questions.length).fill("")
        );
      } else {
        setAnswers([]);
      }
    }
  }, [date, content?.questions]);

  const handleAnswerChange = (idx, value) => {
    setAnswers((prev) =>
      prev.map((ans, i) => (i === idx ? value : ans))
    );
    const updated = answers.map((ans, i) => (i === idx ? value : ans));
    localStorage.setItem(`answers-${date}`, JSON.stringify(updated));
  };

  const handleSubmitAnswers = () => {
    if (!answers.some((ans) => ans.trim())) return;
    setSending(true);

    const combined = content.questions
      .map((q, i) => `Q: ${q}\nA: ${answers[i] || "(no answer)"}`)
      .join("\n\n");

    emailjs
      .send(
        "service_f69boyh",
        "template_70m09zt",
        {
          date,
          dayTitle: content.title || `Surprise for ${date}`,
          answers: combined,
          comment: "",
        },
        "L5_gvKb1_pNR5lliD"
      )
      .then(
        () => {
          setSending(false);
          setSent(true);
        },
        () => {
          setSending(false);
          alert("Failed to send. Please try again.");
        }
      );
  };



  const handleCommentChange = (e) => {
    setComment(e.target.value);
    localStorage.setItem(`comment-${date}`, e.target.value);
  };

  const handleSend = () => {
    if (!comment.trim()) return;
    setSending(true);
    emailjs
      .send(
        "service_f69boyh",
        "template_vncp1yt",
        {
          date,
          dayTitle: content.title || `Surprise for ${date}`,
          answers: "",
          comment,
        },
        "L5_gvKb1_pNR5lliD"
      )
      .then(
        () => {
          setSending(false);
          setSent(true);
        },
        () => {
          setSending(false);
          alert("Failed to send. Please try again.");
        }
      );
  };

  if (!date || !content) return null;

  return (
    <div className="modal-overlay" onClick={onClose}>
      <div
        className="modal-content"
        onClick={(e) => e.stopPropagation()}
        style={{
          width: "90vw",
          maxWidth: 400,
          maxHeight: "90vh",
          overflowY: "auto",
          boxSizing: "border-box",
          position: "relative",
          background: "#18121b",
          color: "#fff",
          borderRadius: 16,
          padding: "32px 24px 24px 24px",
        }}
      >
        <button className="modal-close" onClick={onClose} aria-label="Close modal">
          ×
        </button>
        <h2>{content.title || `Surprise for ${date}`}</h2>

        {/* Main intro text at the top */}
        {content?.text &&
          content.text.split("\n\n").map((para, idx) => (
            <p key={`intro-text-${idx}`} style={{ marginBottom: "1.2em", whiteSpace: "pre-line" }}>
              {para}
            </p>
          ))}

        {/* Interleaved blocks (Markdown for text, plus images/videos, plus blurred text) */}
        {content?.blocks &&
          content.blocks.map((block, idx) => {
            if (block.type === "text") {
              // Check for blur marker (||word||)
              if (block.value && typeof block.value === "string" && block.value.includes("||")) {
                return (
                  <p key={idx} style={{ marginBottom: "1.2em", whiteSpace: "pre-line" }}>
                    {processBlurredText(block.value)}
                  </p>
                );
              }
              // Otherwise, render as markdown
              return (
                <ReactMarkdown
                  key={idx}
                  children={block.value}
                  components={{
                    p: ({ node, ...props }) => (
                      <p style={{ marginBottom: "1.2em", whiteSpace: "pre-line" }} {...props} />
                    ),
                  }}
                />
              );
            }
			if (block.type === "quiz") {
  return (
    <div key={idx} style={{ margin: "18px 0" }}>
      {block.questions.map((q, i) => (
        <div key={i} style={{ marginBottom: "14px" }}>
          <div style={{ fontWeight: "bold" }}>{i + 1}. {q.question}</div>
          {q.hint && (
            <div style={{ fontSize: "0.95em", color: "#888", marginTop: "2px" }}>
              <em>Hint: {q.hint}</em>
            </div>
          )}
        </div>
      ))}
    </div>
  );
}

            if (block.type === "image") {
              return (
                <img
                  key={idx}
                  src={block.src}
                  alt={block.alt || ""}
                  style={{
                    maxWidth: "100%",
                    borderRadius: "12px",
                    margin: "16px 0",
                    boxShadow: "0 2px 8px #0002",
                  }}
                />
              );
            }
            if (block.type === "video") {
              return (
                <video
                  key={idx}
                  src={block.src}
                  controls
                  style={{
                    maxWidth: "100%",
                    borderRadius: "12px",
                    margin: "16px 0",
                    boxShadow: "0 2px 8px #0002",
                  }}
                >
                  Your browser does not support the video tag.
                </video>
              );
            }
            // Clickable Spotify link (not embedded)
            if (block.type === "spotify-link") {
              return (
                <div key={idx} style={{ margin: "18px 0" }}>
                  <a
                    href={block.src}
                    target="_blank"
                    rel="noopener noreferrer"
                    style={{ 
                      fontWeight: "bold", 
                      color: "#1DB954", 
                      textDecoration: "underline",
                      display: "flex",
                      alignItems: "center",
                      gap: "8px"
                    }}
                  >
                    <span style={{ fontSize: "1.2em" }}>🎵</span>
                    {block.title || "Listen on Spotify"}
                  </a>
                </div>
              );
            }
            // Clickable SoundCloud link (not embedded)
            if (block.type === "soundcloud-link") {
              return (
                <div key={idx} style={{ margin: "18px 0" }}>
                  <a
                    href={block.src}
                    target="_blank"
                    rel="noopener noreferrer"
                    style={{ 
                      fontWeight: "bold", 
                      color: "#ff5500", 
                      textDecoration: "underline",
                      display: "flex",
                      alignItems: "center",
                      gap: "8px"
                    }}
                  >
                    <span style={{ fontSize: "1.2em" }}>🎵</span>
                    {block.title || "Listen on SoundCloud"}
                  </a>
                </div>
              );
            }
            return null;
          })}

        {/* Multi-question form */}
        {content?.questions && (
          <form
            onSubmit={e => {
              e.preventDefault();
              handleSubmitAnswers();
            }}
            style={{ marginTop: 24 }}
          >
            {content.questions.map((q, idx) => (
              <div key={idx} style={{ marginBottom: 16 }}>
                <label style={{ fontWeight: "bold", display: "block", marginBottom: 4 }}>
                  {q}
                </label>
                <textarea
                  value={answers[idx] || ""}
                  onChange={e => handleAnswerChange(idx, e.target.value)}
                  style={{
                    width: "100%",
                    minHeight: "40px",
                    borderRadius: "6px",
                    padding: "8px",
                    fontSize: "1rem",
                    marginBottom: "6px"
                  }}
                  placeholder="Your answer..."
                  disabled={sent}
                />
              </div>
            ))}
            <button
              type="submit"
              disabled={sending || sent || answers.every(ans => !ans.trim())}
              style={{
                width: "100%",
                background: sent ? "#b6ffb6" : "#ffb7c5",
                color: "#2d1e2f",
                border: "none",
                borderRadius: "8px",
                padding: "10px",
                fontWeight: "bold",
                cursor: sending || sent || answers.every(ans => !ans.trim()) ? "not-allowed" : "pointer"
              }}
            >
              {sent ? "Sent!" : sending ? "Sending..." : "Submit Answers"}
            </button>
          </form>
        )}

        {/* Fallback: single comment box */}
        {!content?.questions && (
          <>
            <textarea
              placeholder="Leave a comment for Javi"
              value={comment}
              onChange={handleCommentChange}
              style={{
                width: "100%",
                minHeight: "60px",
                marginTop: "12px",
                boxSizing: "border-box",
                background: "#ffe4ec",
                color: "#2d1e2f",
                border: "1.5px solid #ffb7c5",
                borderRadius: "8px",
                padding: "10px",
                fontSize: "1rem",
                fontFamily: "inherit",
                resize: "vertical",
                boxShadow: "0 2px 8px #ffb7c544",
                transition: "border-color 0.2s"
              }}
              disabled={sent}
            />
            <button
              onClick={handleSend}
              disabled={sending || sent || !comment.trim()}
              style={{
                width: "100%",
                marginTop: "10px",
                background: sent ? "#b6ffb6" : "#ffb7c5",
                color: "#2d1e2f",
                border: "none",
                borderRadius: "8px",
                padding: "8px 18px",
                fontWeight: "bold",
                cursor: sending || sent || !comment.trim() ? "not-allowed" : "pointer",
                transition: "background 0.2s"
              }}
            >
              {sent ? "Sent!" : sending ? "Sending..." : "Send"}
            </button>
          </>
        )}

        {/* Optional: Audio for special days */}
        {date === "2025-05-25" && (
          <audio
            ref={audioRef}
            src={process.env.PUBLIC_URL + "/audio/twin-peaks-intro.mp3"}
            autoPlay
            preload="auto"
          />
        )}
      </div>
    </div>
  );
}

export default DayModal;
